<template>
	<div class="login">
		<h2></h2>
		<el-form label-position="right" :model="loginForm" :rules="loginRules" ref="loginForm" label-width="70px" class="loginForm">
			<el-form-item label="" prop="phone" :error="errorMsg">
				<el-input type="phone" v-model="loginForm.phone" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="password">
				<el-input type="password" v-model="loginForm.password" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('loginForm')"></el-button>
				<el-button @click="resetForm('loginForm')"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
  export default {
    name: 'login',
    data () {
      let checkPhone = (rule, value, callback) => {
        const PHONE_REGEXP = /^((13[0-9])|(15[^4])|(18[0-3,5-9])|(17[0-8])|(147))\d{8}$/
        const HONGKONG_PHONE_REGEXP = /^(5|6|8|9)\d{7}$/
        if (value === '') {
          callback(new Error(''))
        } else if (!PHONE_REGEXP.test(value) && !HONGKONG_PHONE_REGEXP.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
      let validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        }
        callback()
      }
      return {
        loginForm: {
          phone: '',
          password: '',
          rememberMe: false
        },
        loginRules: {
          phone: [
            {validator: checkPhone, trigger: 'blur'}
          ],
          password: [
            {validator: validatePassword, trigger: 'blur'}
          ]
        },
				errorMsg: ""
      }
    },
    methods: {
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let user = {
              account: this.loginForm.phone,
              passWord: this.loginForm.password
            }
            this.$store.dispatch('login', user).then((res) => {
              this.loading = false
              this.$message({
								message: '',
								type: 'success'
							});
              this.$router.push({path: '/'})
            }).catch(error => {
							this.errorMsg = ''
            })
          } else {
            alert('error login!')
            return false
          }
        })
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      }
    }
  }
</script>

<style scoped>
  .login {
    background: #fff;
    width: 800px;
    height: 500px;
    padding: 40px 80px;
  }
  .login h2 {
    margin-bottom: 30px;
    text-align: center;
  }
  .loginForm a {
    float: right;
    color: #70bde0;
  }
</style>






















